class Typing {
  constructor(opts) {
    this.opts = opts || {}
    this.source = opts.source
    this.output = opts.output
    this.delay = opts.delay || 120
    this.chain = {
      parent: null,
      dom: this.output,
      val: []
    }
    if (!(typeof this.opts.done === 'function')) this.opts.done = function () {}
  }

  init() {
    //åˆå§‹åŒ–å‡½æ•°
    this.chain.val = this.convert(this.source, this.chain.val)
  }

  convert(dom, arr) {
    //å°†domèŠ‚ç‚¹çš„å­èŠ‚ç‚¹è½¬æ¢æˆæ•°ç»„ï¼Œ
    const children = Array.from(dom.childNodes)
    for (let i = 0; i < children.length; i++) {
      const node = children[i]
      if (node.nodeType === 3) {
        arr = arr.concat(node.nodeValue.split('')) //å°†å­—ç¬¦ä¸²è½¬æ¢æˆå­—ç¬¦ä¸²æ•°ç»„ï¼ŒåŽé¢æ‰“å°æ—¶æ‰ä¼šä¸€ä¸ªä¸€ä¸ªçš„æ‰“å°
      } else if (node.nodeType === 1) {
        let val = []
        val = this.convert(node, val)
        arr.push({
          dom: node,
          val: val
        })
      }
    }
    return arr
  }

  print(dom, val, callback) {
    setTimeout(function () {
      dom.appendChild(document.createTextNode(val))
      callback()
    }, this.delay)
  }

  play(ele) {
    //å½“æ‰“å°æœ€åŽä¸€ä¸ªå­—ç¬¦æ—¶ï¼ŒåŠ¨ç”»å®Œæ¯•ï¼Œæ‰§è¡Œdone
    if (!ele.val.length) {
      if (ele.parent) this.play(ele.parent)
      else this.opts.done()
      return
    }
    const current = ele.val.shift() //èŽ·å–ç¬¬ä¸€ä¸ªå…ƒç´ ï¼ŒåŒæ—¶åˆ é™¤æ•°ç»„ä¸­çš„ç¬¬ä¸€ä¸ªå…ƒç´ 
    if (typeof current === 'string') {
      this.print(ele.dom, current, () => {
        this.play(ele) //ç»§ç»­æ‰“å°ä¸‹ä¸€ä¸ªå­—ç¬¦
      })
    } else {
      const dom = current.dom.cloneNode() //å…‹éš†èŠ‚ç‚¹ï¼Œä¸å…‹éš†èŠ‚ç‚¹çš„å­èŠ‚ç‚¹ï¼Œæ‰€ä»¥ä¸ç”¨åŠ å‚æ•°true
      ele.dom.appendChild(dom)
      this.play({
        parent: ele,
        dom,
        val: current.val
      })
    }
  }

  start() {
    this.init()
    this.play(this.chain)
  }
}

export default Typing
